export class Item {
    nom:String
    prix: number 
    image: string 
    adulte:boolean 
    sexe:String 
    type:String
    id:String
    constructor(nom:String, prix: number, image: string, adulte:boolean, sexe:String, type:String, id?:String){
        this.nom=nom;
        this.prix = prix;
        this.image=image;
        this.adulte = adulte;
        this.sexe = sexe;
        this.type = type;
        this.id=id;
    }
  
}